import { FC } from "react";
import Button, { ButtonProps } from "../Button/Button";
import Image from "next/image";

export type CardProps = {
    imageSrc: string;
    title: string;
    subtitle?: string;
    link?: ButtonProps;
    agentName: string;
    agentImageSrc: string;
};

const Hero: FC<CardProps> = ({
    imageSrc,
    title,
    subtitle,
    link,
    agentName,
    agentImageSrc,
}) => {
    return (
        <div className="hero">
            <div className="card__image-container">
                {imageSrc && (
                    <Image
                        src={imageSrc}
                        alt={title}
                        fill
                        // className="card__image"
                    />
                )}
            </div>
            <div className="card__body card__body--hero">
                <h1 className="card__title card__title--hero">{title}</h1>
                <p className="card__subtitle card__subtitle--hero">
                    {subtitle}
                </p>
                <div className="card__agent-info">
                    <Image
                        src={agentImageSrc}
                        alt={agentName}
                        width={50}
                        height={50}
                        className="card__agent-image"
                    />
                    <span className="card__agent-name">{agentName}</span>
                </div>
            </div>
            <div className="card__button card__button--hero">
                {link && <Button {...link} />}
            </div>
        </div>
    );
};

export default Hero;
